  --button-invert-map: invert(0%);
  --bg-color-navbar: #ffffffe6;
  --border-color-modal: #0003;
  --bg-color-dropdown-hover: #e9ecef;
  --text-color-dropdown-hover: #000;
  --fa-color-navbar: #00000080;
}

[data-theme="dark"] {
  --bg-color: #222;
