  --text-muted-color: #9e9689;
  --bg-color-map-transparant: #181a1b80;
  --bg-color-map-solid: #181a1b;
  --text-color-map-transparant: #e8e6e3bf;
  --text-color-map-solid: #e8e6e3;
  --border-color-map: #7b7265;
  --button-invert-map: invert(100%);
  --bg-color-navbar: #262a2be6;
  --border-color-modal: #8c827333;
  --bg-color-dropdown-hover: #232627;
  --text-color-dropdown-hover: #d9d5d0;
  --fa-color-navbar: #ffffffbf;
}

#loader-wrapper {
  width: 100%;
  height: 100%;
  z-index: 9000;
  background-color: var(--bg-color);
  position: fixed;
  top: 0;
  left: 0;
}

#loader-wrapper #loader-title {
  padding: 15px;
  font-size: 1.25rem;
  position: absolute;
}

#loader-wrapper #loader-status {
  padding: 15px;
  position: absolute;
  bottom: 0;
}

#loader-wrapper #loader-title, #loader-wrapper #loader-status {
  color: var(--text-color);
}

#loader-wrapper #loader {
  width: 150px;
  height: 150px;
  z-index: 1001;
  border: 3px solid #0000;
  border-top-color: purple;
  border-radius: 50%;
  margin: -75px 0 0 -75px;
  animation: spin 2s linear infinite;
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
}

#loader-wrapper #loader:before {
  content: "";
  border: 3px solid #0000;
  border-top-color: #c8c800;
  border-radius: 50%;
  animation: spin 3s linear infinite;
  position: absolute;
  inset: 5px;
}

#loader-wrapper #loader:after {
  content: "";
  border: 3px solid #0000;
  border-top-color: #00a050;
  border-radius: 50%;
  animation: spin 1.5s linear infinite;
  position: absolute;
  inset: 15px;
}

.loaded #loader-wrapper {
  visibility: hidden;
  opacity: 0;
  transition: all .3s ease-out 1s;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#map {
  height: 100vh;
  width: 100%;
  background-color: #ddd;
}

.mapboxgl-ctrl-top-left, .mapboxgl-ctrl-top-right {
  top: 60px;
}

.mapboxgl-ctrl-attrib {
  background-color: var(--bg-color-map-transparant) !important;
  color: var(--text-color-map-transparant) !important;
}

.mapboxgl-ctrl-attrib a {
  color: var(--text-color-map-transparant) !important;
}

.mapboxgl-ctrl-scale {
  border-color: var(--border-color-map);
  background-color: var(--bg-color-map-transparant) !important;
  color: var(--text-color-map-transparant) !important;
}

.mapboxgl-ctrl-group {
  background-color: var(--bg-color-map-solid) !important;
}

.mapboxgl-ctrl-group button > span {
  filter: var(--button-invert-map);
}

.mapboxgl-ctrl-geocoder {
  background-color: var(--bg-color-map-solid) !important;
}

.mapboxgl-ctrl-geocoder svg {
  fill: var(--text-color-map-solid);
}

.mapboxgl-ctrl-geocoder input {
  color: var(--text-color-map-transparant);
}

.mapboxgl-ctrl-geocoder input:focus {
  color: var(--text-color-map-solid);
}

.mapboxgl-ctrl-geocoder .suggestions {
  background-color: var(--bg-color-map-solid) !important;
}

.mapboxgl-ctrl-geocoder .suggestions .active > a, .mapboxgl-ctrl-geocoder .suggestions li > a:hover {
  background-color: var(--bg-color);
}

.mapboxgl-ctrl-geocoder--suggestion-title, .mapboxgl-ctrl-geocoder--suggestion-address {
  color: var(--text-color-map-transparant) !important;
}

.mapboxgl-ctrl-geocoder--button {
  background-color: #0000;
}

.mapboxgl-popup {
  animation: fadein .2s;
}

.mapboxgl-popup-content {
  min-width: 200px;
  padding-bottom: 10px;
}

.mapboxgl-popup-content > .popup-wikidata {
  padding: 6px;
  font-size: .8rem;
}

.mapboxgl-popup-content > .popup-wikidata .popup-name {
  margin-bottom: 6px;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 1.3rem;
  display: inline-block;
  position: relative;
}

.mapboxgl-popup-content > .popup-wikidata .popup-name > .highlight-low {
  height: 100%;
  width: 100%;
  opacity: .5;
  position: absolute;
  top: 0;
  left: 0;
}

.mapboxgl-popup-content > .popup-wikidata .popup-name > .popup-name__name {
  white-space: nowrap;
}

.mapboxgl-popup-content > .popup-wikidata .popup-name > .popup-name__nickname {
  white-space: nowrap;
  font-style: italic;
}

.mapboxgl-popup-content > .popup-wikidata > .popup-description {
  margin-bottom: .5rem;
}

.mapboxgl-popup-content > .popup-wikidata > .popup-links {
  margin-top: 10px;
}

.mapboxgl-popup-content > hr {
  margin: .2rem 0;
}

.mapboxgl-popup-content > .popup-streetname {
  padding: 6px;
}

.mapboxgl-popup-content .popup-edit {
  float: inline-end;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.mapboxgl-popup-tip {
  border-top-color: var(--bg-color-map-solid) !important;
}

.mapboxgl-popup-content {
